.buttonExplore {
  color: #494949 !important;
  text-transform: uppercase;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949 !important;
  border-radius: 6px;
  display: inline-block;
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: button-anim 2s ease forwards 3.7s;
          animation: button-anim 2s ease forwards 3.7s;
  opacity: 0;
  cursor: pointer;
  }
  
  .buttonExplore:hover {
  color: #ffffff !important;
  background: #bd4336;
  border-color: #722a22 !important;
  transition: all 0.4s ease-in-out;
  font-weight: 600;
  }

